"use client"
/* eslint-disable @typescript-eslint/no-unused-vars */
import { useState } from "react";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { 
  Edit2, 
  Users, 
  Search, 
  AlertCircle, 
  CheckCircle2, 
  Clock, 
  XCircle, 
  CalendarClock,
  Trash2 
} from "lucide-react";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import { Alert, AlertDescription } from "@/components/ui/alert";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";

interface Soutenance {
  id: number;
  date: string;
  time: string;
  location: string;
  groupe: string;
  juryNames: string | string[];
  status: string;
}

interface SoutenanceTableProps {
  soutenances?: Soutenance[];
  onEdit?: (id: number) => void;
  onDelete?: (id: number) => void;
}

export default function SoutenanceTable({ 
  soutenances = [], 
  onEdit = (id) => console.log(`Edit soutenance ${id}`),
  onDelete = (id) => console.log(`Delete soutenance ${id}`)
}: SoutenanceTableProps) {
  const [searchTerm, setSearchTerm] = useState("");

  console.log("Soutenances Data:", soutenances);

  const filteredSoutenances = soutenances.filter(soutenance => {
    const groupMatch = soutenance.groupe && soutenance.groupe.toLowerCase().includes(searchTerm.toLowerCase());
  
    const juryNamesMatch =
      typeof soutenance.juryNames === "string"
        ? soutenance.juryNames.toLowerCase().includes(searchTerm.toLowerCase())
        : Array.isArray(soutenance.juryNames) &&
          soutenance.juryNames.some((name) =>
            name && name.toLowerCase().includes(searchTerm.toLowerCase())
          );
  
    return groupMatch || juryNamesMatch;
  });
  
  console.log("Filtered Soutenances:", filteredSoutenances);

  return (
    <Card className="w-full">
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle className="text-2xl">Soutenances</CardTitle>
          <div className="relative w-96">
            <Search className="absolute left-2 top-2.5 h-5 w-5 text-muted-foreground" />
            <Input
              placeholder="Rechercher..."
              value={searchTerm}
              onChange={(e) => {
                console.log("Search Term:", e.target.value);
                setSearchTerm(e.target.value);
              }}
              className="pl-10 text-lg"
            />
          </div>
        </div>
      </CardHeader>
      <CardContent className="p-0">
        <div className="relative w-full overflow-auto">
          <Table>
            <TableHeader className="bg-muted/50">
              <TableRow>
                <TableHead>Date</TableHead>
                <TableHead>Heure</TableHead>
                <TableHead>Lieu</TableHead>
                <TableHead>Groupe</TableHead>
                <TableHead>Jury</TableHead>
                <TableHead>Statut</TableHead>
                <TableHead>Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredSoutenances.map((soutenance) => (
                <TableRow key={soutenance.id}>
                  <TableCell>{soutenance.date}</TableCell>
                  <TableCell>{soutenance.time}</TableCell>
                  <TableCell>{soutenance.location}</TableCell>
                  <TableCell>{soutenance.groupe}</TableCell>
                  <TableCell>{Array.isArray(soutenance.juryNames) ? soutenance.juryNames.join(", ") : soutenance.juryNames}</TableCell>
                  <TableCell>{soutenance.status}</TableCell>
                  <TableCell>
                    <Button onClick={() => {
                      console.log("Editing Soutenance ID:", soutenance.id);
                      onEdit(soutenance.id);
                    }}>
                      <Edit2 className="h-5 w-5" />
                    </Button>
                    <Button onClick={() => {
                      console.log("Deleting Soutenance ID:", soutenance.id);
                      onDelete(soutenance.id);
                    }}>
                      <Trash2 className="h-5 w-5" />
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      </CardContent>
    </Card>
  );
}
